import React, { useState, useEffect } from 'react';
import { Modal, Button, Form, Input, Checkbox, Alert } from 'antd';
//import axios from 'axios';
import axios from "../../service/http-common";
import { PoweroffOutlined } from '@ant-design/icons';
import authHeader from "../../services/auth-header";



const layout = {
    labelCol: {
        span: 8,
    },
    wrapperCol: {
        span: 22,
    },
};

export default function ModalEdit(props) {

    const [form] = Form.useForm();


    const [isLoading, setIsLoading] = useState(false);
    const [check, setCheck] = useState(false);

    const [errorMessage, setErrorMessage] = useState("");

    useEffect(() => {

        form.setFieldsValue({
            prefix: props.node.prefix,
            desc: props.node.description,
            tag: props.node.tag,
            _id: props.node._id

        })
    })




    const handleOk = (values) => {

        setIsLoading(true);

        const error = form.getFieldsError();

        console.log("///////////")
        console.log(error)
        console.log("///////////")

        const data = form.getFieldsValue();

        console.log("/////////////");
        console.log(data);
        console.log("/////////////");

        axios({
            method: 'DELETE',
            url: '/api/test/delete',
            data: {
                ...data,
                ipVer: props.ipVer,
                dbName: props.dbName
            },
            headers: authHeader()
        })

            //axios.delete('http://localhost:8080/api/test/delete',  { data: { foo: "bar" }, headers: { "Authorization": "***" } })
            //axios.delete('http://localhost:8080/api/test/delete', {data:{username:"user", password:"pass"}}, )
            .then(res => {
                setIsLoading(false);
                //setSendMessage(res.data.message);
                //setModalShow(true);
                //props.history.push("/home");

                props.update();
                props.handleCancel();

            }).catch(error => {
                setIsLoading(false);

                const errMessage =
                (error.response &&
                  error.response.data &&
                  error.response.data.message) ||
                error.message ||
                error.toString();
    
                setErrorMessage(errMessage); 
            });
        console.log();

    }

    const onModalCancel = () => {

        form.resetFields();
        setErrorMessage("")

        props.handleCancel();
    }





    return (
        <div>




            <Modal
                visible={props.showModal}
                title="Usuń prefix"
                onOk={handleOk}
                onCancel={onModalCancel}
                footer={[
                    <Button key="back" onClick={onModalCancel}>
                        Zamknij
            </Button>,
                    <Button key="submit" type="primary" form="basic" /*loading={loading}*/ onClick={handleOk} loading={isLoading}
                    >
                        Usuń
                        </Button>
                ]}
            >

                {errorMessage && <Alert message={errorMessage} style={{ marginBottom: "10px" }} type="error" />}
                <Form
                    form={form}
                    {...layout}
                //name="basic"
                // initialValues={{
                //     remember: true,
                // }}
                //onFinish={onFinish}
                //onFinishFailed={onFinishFailed}
                >

                    <Form.Item label="_id" name="_id" shouldUpdate={true} hidden={true} initialValue={props.node._id}
                    // rules={[
                    //     {
                    //         required: true,
                    //         message: 'Wubierz wersje IP!',
                    //     }]}
                    >
                        <Input style={{ color: "black" }} disabled={true} />
                    </Form.Item>

                    <Form.Item label="Prefix" name="prefix" initialValue={props.node.prefix}
                    // rules={[
                    //     {
                    //         required: true,
                    //         message: 'Wubierz wersje IP!',
                    //     }]}
                    >
                        <Input style={{ color: "black" }} disabled={true} />
                    </Form.Item>

                    <Form.Item label="Opis" name="desc" initialValue={props.node.description}
                    // rules={[
                    //     {
                    //         required: true,
                    //         message: 'Pole "Opis" jest wymagane!',
                    //     }]}
                    >
                        <Input style={{ color: "black" }} disabled={true}/*defaultValue={props.node.description}*/ />
                    </Form.Item>

                    <Form.Item label="TAG" name="tag" initialValue={props.node.tag}
                    // rules={[
                    //     {
                    //         required: true,
                    //         message: 'Pole TAG jest wymagane!',
                    //     }]}
                    >
                        <Input style={{ color: "black" }} disabled={true}/*defaultValue={props.node.tag}*/ />
                    </Form.Item>
                    <Form.Item label="Usuń z podsieciami" name="delete_with_children" initialValue={false} valuePropName="checked">

                        <Checkbox />

                    </Form.Item>


                </Form>

            </Modal>

        </div>
    )
}
