const sortFun = (a, b) => {

  //console.log(`a.id = ${a.id}`)
  //let a = el1.prefix;
  //let b = el2.prefix;

  a = a.split(".").join(",").split("/").join(",").split(",");
  b = b.split(".").join(",").split("/").join(",").split(",");


  return (a[0] - b[0]) || a[1] - b[1] || a[2] - b[2] || a[3] - b[3]
}

