  meSaveRow = async (editedRow) => 
{

  try {
    //const row = await this.formRef;
    const newData = [...this.state.editedRows];
    const index = newData.findIndex((item) => editedRow.prefix === item.prefix);

    if (index > -1) {
      const item = newData[index];
      newData.splice(index, 1, { ...item, ...editedRow });
      this.setState({editedRows: newData});
      //setEditingKey('');
    } 
    else {
      newData.push(editedRow);
      this.setState({editedRows: newData});
      //setEditingKey('');
    }
    
  } catch (errInfo) {
    console.log('meSetState Failed:', errInfo);
  }


}


