  componentDidMount() {

    console.log("Tree component is mount")

    this.setState({ isLoading: true }, () => {
      axios.get(API_URL, { params: { ver: this.props.match.params.ver, dbName: this.props.match.params.id }, headers: authHeader() })
        .then(result => {
          this.setState({
            isLoading: false,
            sourceData: [...result.data.sort(this.props.match.params.ver == "ipv4" ? sortSourceIpv4 : sortSourceIpv6)]
          });

          console.log("!!!componentDidMount!!!");
          console.log(result);
          console.log(`id=${this.props.match.params.id} + ver=${this.props.match.params.id}`);
          this.setState({ loadingError: null });

        })
        .catch(error => {

          const errMessage =
            (error.response &&
              error.response.data &&
              error.response.data.message) ||
            error.message ||
            error.toString();




          this.setState({ isLoading: false, loadingError: errMessage });

        });
    });

  }

