import React, { useState, useEffect } from 'react';
import { Modal, Button, Form, Input, Alert } from 'antd';
//import axios from 'axios';
import axios from "../../service/http-common";
import { PoweroffOutlined } from '@ant-design/icons';
import authHeader from "../../services/auth-header";



const layout = {
    labelCol: {
        span: 8,
    },
    wrapperCol: {
        span: 22,
    },
};

export default function ModalEdit(props) {

    const [form] = Form.useForm();
    

    const [isLoading, setIsLoading] = useState(false);
    const [errorMessage, setErrorMessage] = useState("");

    useEffect(() => {

        form.setFieldsValue({prefix: props.node.prefix, 
                            desc:   props.node.description, 
                            tag:   props.node.tag 
                             
        
        })
    },[props] )




    const handleOk = (values) => {

        form.submit();
    
    }


const formOk = (values) => {

    setIsLoading(true);

    const error = form.getFieldsError();

    console.log("///////////")
    console.log(error)
    console.log("///////////")

    const data = form.getFieldsValue();

    axios.patch('/api/test/edit', {...data, ipVer: props.ipVer, dbName: props.dbName } , 
       { headers: authHeader() }
    ).then(res => {
        setIsLoading(false);
        //setSendMessage(res.data.message);
        //setModalShow(true);
        //props.history.push("/home");

        props.update();
        props.handleCancel();
        //window.location.reload();

    })
        .catch(error => {
            setIsLoading(false);
            // console.log(err)
            // if(err.response.data.message)
            // setErrorMessage(err.response.data.message);
            // else
            // setErrorMessage(err.message);

            const errMessage =
            (error.response &&
              error.response.data &&
              error.response.data.message) ||
            error.message ||
            error.toString();

            


            setErrorMessage(errMessage);        



        });
    console.log();
    
}

const onModalCancel = () => {

    form.resetFields();
    setErrorMessage("")

    props.handleCancel();
}



    return (
        <div>




            <Modal
                visible={props.showModal}
                title="Edytuj prefix"
                onOk={handleOk}
                onCancel={onModalCancel}
                footer={[
                    <Button key="back" onClick={onModalCancel}>
                        Zamknij
            </Button>,
                      <Button key="submit" htmlType="submit" type="primary"  form="basic" /*loading={loading}*/ onClick={handleOk}  loading={isLoading}
                      >
                        Zapisz
                        </Button>
                ]}
            >

                {errorMessage&&<Alert message={errorMessage} style={{ marginBottom: "10px" }} type="error" />}
                <Form
                    form={form}
                    {...layout}
                    //name="basic"
                    // initialValues={{
                    //     remember: true,
                    // }}
                    onFinish={formOk}
                    //onFinishFailed={() => console.log("onFinishFailed")}
                >

                    <Form.Item label="Prefix" name="prefix" initialValue={props.node.prefix} shouldUpdate={true}
                    // rules={[
                    //     {
                    //         required: true,
                    //         message: 'Wubierz wersje IP!',
                    //     }]}
                    >
                        <Input style={{ color: "black" }} disabled={true} defaultValue={props.node.prefix} value={props.node.prefix} />
                    </Form.Item>

                    <Form.Item label="Opis" name="desc"
                    rules={[
                        {
                            required: true,
                            message: 'Pole "Opis" jest wymagane!',
                        }]}
                    >
                        <Input style={{ color: "black" }} /*defaultValue={props.node.description}*/ defaultValue={props.node.description} />
                    </Form.Item>

                    <Form.Item label="TAG" name="tag"
                    rules={[
                        {
                            required: true,
                            message: 'Pole TAG jest wymagane!',
                        }]}
                    >
                        <Input style={{ color: "black" }} /*defaultValue={props.node.tag}*/ defaultValue={props.node.tag} />
                    </Form.Item>


                </Form>

            </Modal>

        </div>
    )
}
