import React, { useState, useEffect } from 'react';
//import ReactDOM from 'react-dom';
import 'antd/dist/antd.css';
//import './index.css';
import { Table, Input, InputNumber, Popconfirm, Form } from 'antd';
import { element } from 'prop-types';

import { EditOutlined, EditTwoTone, EditFilled, SaveOutlined, SaveFilled, CloseCircleOutlined  } from '@ant-design/icons';






const EditableCell = ({
  editing,
  dataIndex,
  title,
  inputType,
  record,
  index,
  children,
  ...restProps
}) => {
  const inputNode = inputType === 'number' ? <InputNumber /> : <Input disabled={title=="Prefix"?true:false} />;
  return (
    <td {...restProps}>
      {editing ? (
        <Form.Item

          //disabled={true}
          name={dataIndex}
          style={{
            margin: 0,
          }}
          rules={[
            {
              required: true,
              message: `Uzupełnij pole ${title}!`,
            },
          ]}
        >
          {inputNode}
        </Form.Item>
      ) : (
        children
      )}
    </td>
  );
};

const EditableTable = (props) => {
  const [form] = Form.useForm();
  const [data, setData] = useState([]);
  //const [data, setData] = useState(props.dataSource.map(obj=> ({ ...obj, touched: 'false' })));
  const [editingKey, setEditingKey] = useState('');

  useEffect(() => {

    console.log("EditableTable mount/update")

    setData(props.dataSource.map(obj=> ({ ...obj, touched: 'false' })))


  }, [props.dataSource])

  const isEditing = (record) => record.prefix === editingKey;

  const edit = (record) => {
    form.setFieldsValue({
      prefix: '',
      description: '',
      tag: '',
      ...record,
    });
    setEditingKey(record.prefix);
  };

  const cancel = () => {
    setEditingKey('');
  };

  const save = async (prefix) => {
    try {

      const row = await form.validateFields();
      const newData = [...data];
      const index = newData.findIndex((item) => prefix === item.prefix);

      if (index > -1 && (row.tag !== newData[index].tag || row.description !== newData[index].description )) {
        const item = newData[index];
        //meSetState();

        //const rowEdited = {...item,...row};
        newData.splice(index, 1, { ...item, ...row, touched: "true" });
        setData(newData);
        props.saveEditedRow(row);
        setEditingKey('');
       } else {
      //   newData.push(row);
      //   setData(newData);
      //   props.saveEditedRow(row);
         setEditingKey('');
       }
    } catch (errInfo) {
      console.log('Validate Failed:', errInfo);
    }
  };

  const columns = [
    {
      title: 'Prefix',
      dataIndex: 'prefix',
      width: '25%',
      editable: true,
      //key: 'prefix'
    },
    {
      title: 'Description',
      dataIndex: 'description',
      width: '35%',
      editable: true,
    },
    {
      title: 'Tag',
      dataIndex: 'tag',
      width: '20%',
      editable: true,
    },
    // {
    //     title: 'Touched',
    //     dataIndex: 'touched',
    //     width: '10%',
    //     render: touched => (

    //       <span>

    //       {touched=="true"? <EditFilled /> : <EditOutlined/>}
    //       </span>
        
    //     ),
      
    //   },
    {
      title: 'Operation',
      dataIndex: 'operation',
      render: (_, record) => {
        const editable = isEditing(record);
        return editable ? (
          <span>
            <a
              href="javascript:;"
              onClick={() => save(record.prefix)}
              style={{
                marginRight: 8,
              }}
            >
              <SaveOutlined/>
            </a>
            {/* <Popconfirm title="Sure to cancel?" onConfirm={cancel}> */}
              <a href="javascript:;" onClick={cancel}><CloseCircleOutlined/></a>
            {/* </Popconfirm> */}
          </span>
        ) : (
          <a href="javascript:;" disabled={editingKey !== ''} onClick={() => edit(record)}>
            {record.touched=="true"? <EditFilled /> : <EditOutlined/>}
          </a>
        );
      },
    },
  ];
  const mergedColumns = columns.map((col) => {
    if (!col.editable) {
      return col;
    }

    return {
      ...col,
      onCell: (record) => ({
        record,
        inputType: col.dataIndex === 'age' ? 'number' : 'text',
        dataIndex: col.dataIndex,
        title: col.title,
        editing: isEditing(record),
      }),
    };
  });
  return (
    <Form form={form} component={false}>
      <Table
        components={{
          body: {
            cell: EditableCell,
          },
        }}
        bordered
        dataSource={data}
        columns={mergedColumns}
        rowClassName="editable-row"
        pagination={{
          onChange: cancel,
        }}
      />
    </Form>
  );
};

export default EditableTable; 

//ReactDOM.render(<EditableTable />, document.getElementById('container'));