import React, { useState, useEffect, useCallback } from "react";
import socketIOClient from "socket.io-client";
import { Form } from 'react-bootstrap';
//import { Switch, Route, Link } from "react-router-dom";
import UploadService from '../service/FileUploadService';
import UserService from "../services/user.service";
//import styled from 'styled-components';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'antd/dist/antd.css';
import { Steps, Row as Rowa, Col as Cola, Radio, Input, Select, Button as Buttona, Form as Forma, Statistic } from 'antd';
//import icon from 'antd/lib/icon';
//import * as icon from "@ant-design/icons";

//const { UploadOutlined } = icon;
const { Step } = Steps;

const { Option } = Select;

var FormData = require('form-data');


const ENDPOINT = "http://ip.regnumtuum.com:8080";




const SocketExample = (props) => {
    //const imageInputRef = useRef(null);
    const [response, setResponse] = useState("");
    const [selectedFile, setSelectedFile] = useState(undefined);
    //const [progress, setProgress] = useState(0);
    //const [currentFile, setCurrentFile] = useState(undefined);
    const [messageInputFile, setMessageInputFile] = useState("");
    const [message, setMessage] = useState("");
    const [messageSuccess, setMessageSuccess] = useState("");


    const [content, setContent] = useState([]);
    const [ipVer, setIpVer] = useState("");
    const [dbName, setDbName] = useState("");
    const [ipv4Check, setIpv4Check] = useState(false);
    const [ipv6Check, setIpv6Check] = useState(false);
    const [check, setCheck] = useState("false")
    const [step0Error, setStep0Error] = useState("");
    const [step1Error, setStep1Error] = useState("");
    const [step2Error, setStep2Error] = useState("");
    const [step3Error, setStep3Error] = useState("");
    const [fileInputValue, setFileInputValue] = useState("");
    const [curentStep, setCurentStep] = useState(0)
    const [curentStepStatus, setCurentStepStatus] = useState("process")
    const [isLoading, setIsLoading] = useState(false);
    const [key, setKey] = useState(1);
    const [loadProgress, setLoadProgress] = useState(0)
    const [completed, setCompleted] = useState(false);
    const [lines, setLines] = useState(0);
    const [loadSuccess, setLoadSuccess] = useState(0);
    const [reportFileName, setReportFileName] = useState("");


    const handleSwitchv4 = (e) => {

        console.log(e.target);
        setIpv4Check(true);
        setIpv6Check(false);
        // setIpVer(e.target.id.toLowerCase());
        setIpVer(e.target.id);
        props.history.push(`${props.match.url}/${e.target.id.toLowerCase()}`);

        //window.location.reload();

    }

    const handleSwitchv6 = (e) => {
        setIpv4Check(false);
        setIpv6Check(true);
        console.log(e.target);
        setIpVer(e.target.id.toLowerCase());
        props.history.push(`${props.match.url}/${e.target.id.toLowerCase()}`);

        //window.location.reload();

    }


    useEffect(() => {
        // if(ipv4Check)
        // props.history.push(`/home/ipv4`);
        // if(ipv6Check)
        // props.history.push(`/home/ipv6`);

        const socket = socketIOClient(ENDPOINT);
        // socket.on("FromAPI", data => {
        //     setResponse(data);
        // });

        socket.on("loading_start", data => {
            console.log("loading_start");

            setLoadProgress(0);



        });
        socket.on("loading", data => {
            console.log("loading");
            setLoadProgress(data);
        });

        socket.on("loading_done", box => {

            console.log("loading_done");
            //console.log(box);

           

            setLines(box.lines);
            setLoadSuccess(box.success);
            setReportFileName(box.uuid);

        }
        
        );

        if (props.location.pathname == "/socketexample") {
            setIpv4Check(false);
            setIpv6Check(false);
        }



        if (ipVer) {
            UserService.getDbs(ipVer).then(
                (response) => {
                    console.log(response.data);
                    setContent(response.data);
                },
                (error) => {
                    const _content =
                        (error.response && error.response.data) ||
                        error.message ||
                        error.toString();

                    //setContent(_content);
                }
            );
        }

        return () => socket.disconnect();

    }, [ipVer, /*selectedFile, response*/]);




    const selectFile = (event) => {
        console.log("selectFile!!!");
        console.log(event.target);
        if (event.target.files[0].type != "application/vnd.ms-excel") {
            setMessageInputFile("Proszę wybrać plik z rozszezreniem .csv");
            setSelectedFile(undefined);
            setCurentStep(2);
            setCurentStepStatus("error")
            return;
        }
        if (event.target.files[0].size > 200000) {
            setMessageInputFile("Przekroczono masymalny rozmiar pliku. Maksymalny rozmiar to 200kB.");
            setSelectedFile(undefined);
            setCurentStep(2);
            setCurentStepStatus("error")
            return;
        }
        setSelectedFile(
            event.target.files[0],
        );
        setFileInputValue(event.target.files[0].name)
        //fileInput.current = event.target.files[0];
        setMessageInputFile("");
        setCurentStep(3);
        setCurentStepStatus("process")
    }


    /////////////////////
    const backToStep = (stepNo) => {


        switch (stepNo) {
            case 0:
                console.log('case0');
                setSelectedFile(undefined);
                setIpVer("");
                setDbName("");
                setCurentStep(0)
                setCurentStepStatus("wait")
                break;
            case 1:
                console.log('case1');
                setSelectedFile(undefined);
                setDbName("");
                setCurentStep(1)
                setCurentStepStatus("wait")
                break;
            case 2:
                console.log('case2');
                setSelectedFile(undefined);
                setCurentStep(2)
                setCurentStepStatus("wait")
                break;
            default:
                console.log(`case default`);
        }

        setSelectedFile(undefined);
        setIpVer("");
        setDbName("");


    }

    const upload = () => {
        let currentFile = selectedFile;

        //setProgress(0);
        //setCurrentFile(currentFile);

        var formData = new FormData();
        formData.append("ipVer", ipVer);
        formData.append("dbName", dbName);
        formData.append("file", selectedFile);

        console.log(formData);
        setIsLoading(true);



        // instance.post('/upload', formData,  {
        //     headers: {
        //       'Content-Type': 'multipart/form-data'
        //     }
        // });


        //UploadService.upload(currentFile)
        UploadService.upload(formData)
            .then((response) => {
                setMessageSuccess(Object.values(response.data));
                setIsLoading(false);
                setCompleted(true);
                setSelectedFile(undefined);
                //setIpVer("");
                //setDbName("");
                setCurentStepStatus("finish");
                //backToStep(0);
            })
            .catch((err) => {
                console.log(err);
                //setProgress(0);
                //setMessage(err.response.data);
                //setStep3Error(err.response.data);
                setStep3Error(err);
                setCurentStepStatus("error");
                setIsLoading(false);
                //setCurrentFile(undefined);
            });

        //setSelectedFile(undefined);
    };

    const listItems = content.map(d =>
        <Option key={d.subType} >{d.subType}</Option>

    )
    const dbNameOnChange = (e) => {

        //props.history.push(`${props.location.pathname}/${e.target.value}`);
        setDbName(e);
        setSelectedFile(undefined);
        setCurentStep(2);
        //fileInput.current = null;
        //imageInputRef.current.value = null
        setKey(key + 1);

        console.log(`e=${e}`)
    };

    const handleCheck = (e) => {

        console.log(e.target);
        setCheck(e.target.checked);
    };

    const memoizedCallback = useCallback(
        () => {
            console.log("test sellect file!!")
        },
        [],
    );

    const ipVerOnChange = (e) => {

        console.log(e.target.value)
        setIpVer(e.target.value);
        setDbName("");
        setSelectedFile(undefined);
        setCurentStep(1);


    }

    const radioStyle = {
        display: 'block',
        height: '30px',
        lineHeight: '30px',
    };

    const fInputOnclick = (e) => {

        console.log(e.target)
    }
    const loadNext = () => {

        setCompleted(false);
        setCurentStep(0);
        setCurentStepStatus("process");
        setLoadProgress(0);
        setIpVer("");
        setDbName("");

    }

    const redirecToBase = () => {

        props.history.push(`/dbs/${ipVer}/${dbName}`);


    }

    const takeRaport = () => {

        UserService.getRaport(reportFileName);

        


    } 
    /////////////////////

    return (





        <div className="container mt-3">
            <Rowa style={{ margin: "8px" }} >
                <Cola span={18} offset={3} style={{ border: '1px solid #f0f0f0', borderRadius: '5px', marginTop: "20px" }}>

                    <Steps current={curentStep} status={curentStepStatus} percent={loadProgress} style={{ margin: "8px" }} >
                        <Step title="Wersja IP" description={step0Error} style={{ margin: "8px" }} />
                        <Step title="Nazwa Bazy IP" ddescription={step1Error} style={{ margin: "8px" }} />
                        <Step title="PLIK" description={step2Error} style={{ margin: "8px" }} />
                        <Step title="Załaduj" description={isLoading ? "Loading..." : null} style={{ margin: "8px" }} />
                    </Steps>
                </Cola>
            </Rowa>


            {completed ? <Rowa align="middle"  >
                <Cola span={12} offset={6} style={{
                    border: '1px solid #f0f0f0', borderRadius: '5px', marginBottom: "10px", display: "flex",
                    justifyContent: "center"
                }}>


                    <Statistic title={`Załadowano do bazy ${dbName}:  `} value={`${loadSuccess} z ${lines} prefix`} />
                </Cola>


                <Cola span={12} offset={6} style={{
                    border: '1px solid #f0f0f0', borderRadius: '5px', marginBottom: "10px", display: "flex",
                    justifyContent: "center"
                }}>


                    <Buttona style={{ margin: 16 }} type="primary" onClick={takeRaport}>
                        Pobierz raport 
                    </Buttona>

                    <Buttona style={{ margin: 16 }} type="primary" onClick={loadNext}>
                        Załaduj kolejne
                        </Buttona>
                    <Buttona style={{ margin: 16 }} type="primary" onClick={redirecToBase}>
                        Przejdź do bazy
                        </Buttona>

                </Cola>
            </Rowa> : null}




            {!completed ? <Rowa >
                <Cola span={12} offset={6} style={{ border: '1px solid #f0f0f0', borderRadius: '5px', marginBottom: "10px", marginTop: "20px" }}>

                    <Rowa >
                        <Cola span={12} >
                            <Form.Label as="legend" column sm={4}>
                                IP wersja
                            </Form.Label>
                        </Cola>


                        <Cola span={12}>

                            <Radio.Group value={ipVer} onChange={ipVerOnChange} >
                                <Radio style={radioStyle} value={"ipv4"} id="IPv4" disabled={isLoading} >
                                    IPv4
                                </Radio>
                                <Radio style={radioStyle} value={"ipv6"} id="IPv6" disabled={isLoading}>
                                    IPv6
                                </Radio>
                            </Radio.Group>

                        </Cola>
                    </Rowa>

                </Cola>

            </Rowa> : null}




            {ipVer !== "" && !completed ? <Rowa >
                <Cola span={12} offset={6} style={{ border: '1px solid #f0f0f0', borderRadius: '5px', marginBottom: "10px" }}>


                    <Rowa align="middle">

                        <Cola span={12} >
                            <Form.Label as="legend" column sm={6}>
                                Nazwa Bazy IP
                            </Form.Label>
                        </Cola>

                        <Cola span={12} >


                            <Select value={dbName} style={{ width: "90%" }} onChange={dbNameOnChange} disabled={isLoading}>
                                {listItems}
                            </Select>
                        </Cola>
                    </Rowa>
                </Cola>
            </Rowa> : null}


            {dbName && !completed ? <Rowa align="middle" >
                <Cola span={12} offset={6} style={{ border: '1px solid #f0f0f0', borderRadius: '5px', marginBottom: "10px" }}>
                    <Rowa align="middle">
                        <Cola span={12} >
                            <Form.Label as="legend" column sm={6}>
                                Wybierz plik
                            </Form.Label>
                        </Cola>
                        <Cola span={12} >

                            {/* <Upload name="file" accept=".csv">
                                <Buttona icon={<UploadOutlined />} style={{ marginTop: "10px", marginBottom: "10px",width:"130%"  }}>Click to Upload</Buttona>
                            </Upload> */}

                            <Forma >
                                <Forma.Item style={{ width: "90%", marginTop: "10px", marginBottom: "10px" }}

                                    validateStatus={messageInputFile ? "error" : "success"}
                                    help={messageInputFile}
                                >
                                    <Input placeholder="unavailable choice" type="file" id="error" onChange={selectFile} key={key} disabled={isLoading} />
                                </Forma.Item>
                            </Forma>
                        </Cola>

                    </Rowa>



                </Cola>
            </Rowa> : null}

            {selectedFile ? <Rowa align="middle"  >
                <Cola span={12} offset={6} style={{
                    border: '1px solid #f0f0f0', borderRadius: '5px', marginBottom: "10px", display: "flex",
                    justifyContent: "center"
                }}>


                    <Buttona style={{ marginTop: "10px", marginBottom: "10px" }} onClick={upload} disabled={isLoading}>Załaduj</Buttona>


                </Cola>
            </Rowa> : null}











            {/* </Switch>

            </div> */}

            {/* 
            <div className="custom-file">
                <input
                    type="file"
                    className="custom-file-input"
                    id="validatedCustomFile"
                    required
                />
                <label
                    className="custom-file-label"
                    htmlFor="validatedCustomFile"
                >
                    Choose file...
          </label>
                <div className="invalid-feedback">
                    Example invalid custom file feedback
          </div>
            </div> */}








            {/* It's <time dateTime={response}>{response}</time> */}
        </div>
    );
}

export default SocketExample;