import React, { useState, useEffect } from "react";
import { Switch, Route, Link } from "react-router-dom";
import "bootstrap/dist/css/bootstrap.min.css";
import "./App.css";
import { Tree } from './Tree';

import AuthService from "./services/auth.service";

import Login from "./components/Login";
import Register from "./components/Register";
import Home from "./components/Home";
import Profile from "./components/Profile";
import BoardUser from "./components/BoardUser";
import BoardModerator from "./components/BoardModerator";
import BoardAdmin from "./components/BoardAdmin";
import SocketExample from "./components/SocketExample";
//import AddDb from "./components/AddDb";
//import testmilti from "./components/test_multi";
import axios from "./service/http-common";

const App = () => {
  const [showModeratorBoard, setShowModeratorBoard] = useState(false);
  const [showAdminBoard, setShowAdminBoard] = useState(false);
  const [currentUser, setCurrentUser] = useState(undefined);

  useEffect(() => {
    const user = AuthService.getCurrentUser();

    if (user) {
      setCurrentUser(user);
      setShowModeratorBoard(user.roles.includes("ROLE_MODERATOR"));
      setShowAdminBoard(user.roles.includes("ROLE_ADMIN"));
    }
  }, []);

  const logOut = () => {
    AuthService.logout();
    setCurrentUser(undefined);
    setShowModeratorBoard(false);
    setShowAdminBoard(false);
    //this.props.history.push("/");
  };

  

  return (

 
    <div>
      <nav className="navbar navbar-expand navbar-dark bg-dark">
      {currentUser && (<Link to={"/"} className="navbar-brand">
          Bazy IP
        </Link>)}
        <div className="navbar-nav mr-auto">
          {/* <li className="nav-item">
            <Link to={"/home"} className="nav-link">
              Home
            </Link>
          </li> */}

          {showAdminBoard && (
            <li className="nav-item">
              <Link to={"/backup"} className="nav-link">
                Backup Bazy
              </Link>
            </li>
          )}

          {/* {showAdminBoard && (
            <li className="nav-item">
              <Link to={"/admin"} className="nav-link">
                Admin Board
              </Link>
            </li>
          )} */}

          {/* {currentUser && (
            <li className="nav-item">
              <Link to={"/user"} className="nav-link">
                User
              </Link>
            </li>
          )} */}

          {/* {currentUser && (
            <li className="nav-item">
              <Link to={"/tree"} className="nav-link">
                Tree
              </Link>
            </li>
          )} */}

          {showModeratorBoard && (
            <li className="nav-item">
              <Link to={"/importFromFile"} className="nav-link">
                Import z Pliku
              </Link>
            </li>
          )}

          {/* {currentUser && (
            <li className="nav-item">
              <Link to={"/addDb"} className="nav-link">
                AddDB
              </Link>
            </li>
          )} */}
        </div>







        {currentUser ? (
          <div className="navbar-nav ml-auto">
            <li className="nav-item">
              <Link to={"/profile"} className="nav-link">
                {currentUser.username}
              </Link>
            </li>
            <li className="nav-item">
              {/* <a href="/login" className="nav-link" onClick={logOut}> */}
              <Link to={"/login"} className="nav-link" onClick={logOut}>
                LogOut
              </Link>
              {/* </a> */}
            </li>
          </div>
        ) : (
            <div className="navbar-nav ml-auto">
              <li className="nav-item">
                <Link to={"/login"} className="nav-link">
                  Login
              </Link>
              </li>

              <li className="nav-item">
                <Link to={"/register"} className="nav-link">
                  Sign Up
              </Link>
              </li>
            </div>
          )}
      </nav>

      <div className="container mt-3">
        <Switch>
          <Route exact path="/home/:id/:ver" component={Tree} />
          <Route path="/home/:id" component={Home} />
          <Route exact path={["/","/home"]} component={currentUser?Home:null} />
          
          <Route exact path="/login" component={Login} />
          <Route exact path="/register" component={Register} />
          <Route exact path="/profile" component={Profile} />
          {/* <Route path="/user" component={BoardUser} /> */}
          {/* <Route path="/mod" component={BoardModerator} /> */}
          <Route path="/backup" component={BoardAdmin} />
          <Route exact path="/dbs/:ver/:id" component={Tree} />
          
          <Route path="/importFromFile" component={SocketExample} />
          {/* <Route path="/testmulti" component={testmilti} /> */}
          {/* <Route path="/addDb" component={AddDb} /> */}
          <Route component={() =><div>
            <h1>404 - Not Found!</h1>
          </div>} />

        </Switch>
      </div>
    </div>
  );
};

export default App;

