import React, { useRef, useState, Fragment} from "react";
import { Row as Rowa, Col as Cola, Radio, Input, Select, Button as Buttona, Form as Forma, Checkbox, Card } from 'antd';
import BorderWrapper from 'react-border-wrapper'
import 'antd/dist/antd.css';

const layout = {
    labelCol: {
      span: 6,
    },
    wrapperCol: {
      span: 22,
    },
  };

const MultiItem = (props) => {

    const refIpVer = useRef();
    const refPrefix = useRef();
    const refDesc = useRef();
    const refTag = useRef();
    const refCheck = useRef();
    const [checkVal, setCheckVal]=useState(false);
    const [errorDesc, setErrorDesc] = useState(false);
    const [errorTag, setErrorTag] = useState(false);


    const onInputChange = (e) => {

        console.log("=====================================");
        console.log(`ipVer: default=${refIpVer.current.props.defaultValue}, curent=${refIpVer.current.input.value}`);
        console.log(`prefix: default=${refPrefix.current.props.defaultValue}, curent=${refPrefix.current.input.value}`);
        console.log(`desc: default=${refDesc.current.props.defaultValue}, curent=${refDesc.current.input.value}`);
        console.log(`desc: default=${refTag.current.props.defaultValue}, curent=${refTag.current.input.value}`);
        console.log("=====================================");


        

        console.log(refCheck.current);

        console.log(e.currentTarget.id);

        let target = false;

        switch (e.currentTarget.id) {

            case "ipVer": 
            target = e.currentTarget.value==refIpVer.current.props.defaultValue; 
            break; 

            case "prefix":
            target = e.currentTarget.value==refPrefix.current.props.defaultValue; 
            break;

            case "desc":
            target = e.currentTarget.value==refDesc.current.props.defaultValue; 
            break;    

            case "tag":  
            target = e.currentTarget.value==refTag.current.props.defaultValue; 
            break;  

            default:
            console.log(`No find case option.`);     

        }


        if (e.currentTarget.id!="ipVer"){
            target=refIpVer.current.props.defaultValue==refIpVer.current.input.value&&target;
        }

        if (e.currentTarget.id!="prefix"){
            target=refPrefix.current.props.defaultValue==refPrefix.current.input.value&&target;
            
        }

        if (e.currentTarget.id!="desc"){
            target=refDesc.current.props.defaultValue==refDesc.current.input.value&&target;
            
        }

        if (e.currentTarget.id!="tag"){
            target=refTag.current.props.defaultValue==refTag.current.input.value&&target;
            
        }
        console.log("///////////////////////////");
        console.log(`target = ${target}`); 
        console.log(`errorDesc = ${errorDesc}`);
        console.log(`errorTag = ${errorTag}`);
        console.log("///////////////////////////");

        setCheckVal(!target);

    } 

    const onCheckClick = () => {

        setCheckVal(!checkVal);
    }

    return (

//         <div style={{ border: '1px solid #f0f0f0', borderRadius: '5px', marginBottom: "20px", marginTop: "10px", paddingTop: "20px"}}>
// <h1 style={   { 

// textAlign: "center",
// marginTop: "-30px",
// paddingLeft: "10px",
// height: "20px",
// lineHeight: "20px",
// fontSize: "15px"
// }}>
//     <span style={{
//     backgroundColor: "white"
// }} >
        
// {props.count}</span></h1>


<>
        {/* <Card style={{marginBottom: "5px"} }   hoverable="true" > */}
        {/* <Forma
        {...layout}
        > */}

            {/* <Forma.Item hidden={true}
                label="Warsja IP"
                name="ipVer"
                // rules={[
                //     {
                //         required: true,
                //         message: 'Pole wersja IP jest wymagane!',
                //     }
                //     // { min: 5, message: 'Username must be minimum 5 characters.' },
                //     // {
                //     //     pattern:/^[0-9]*$/,
                //     //     message: `Password Pattern`
                //     //  }
                // ]}
            > */}
            <label for="ipVer">Male</label>
                <input ref={refIpVer} name="ipVer" defaultValue={props.ipVer} onChange={onInputChange}/>
            {/* </Forma.Item> */}
{/* 
            <Forma.Item hidden={true}
                label="Nazwa bazy danych"
                name="dbName"
                // rules={[
                //     {
                //         required: true,
                //         message: 'Pole wersja IP jest wymagane!',
                //     }
                //     // { min: 5, message: 'Username must be minimum 5 characters.' },
                //     // {
                //     //     pattern:/^[0-9]*$/,
                //     //     message: `Password Pattern`
                //     //  }
                // ]}
            > */}
                <input ref={refIpVer} name="dbName" defaultValue={props.dbName} onChange={onInputChange}/>
            {/* </Forma.Item> */}

            {/* <Forma.Item
                label="Prefix"
                name="prefix"
                
                rules={[
                    {
                        required: true,
                        message: 'Pole prefix jest wymagane!',
                    }
                    // { min: 5, message: 'Username must be minimum 5 characters.' },
                    // {
                    //     pattern:/^[0-9]*$/,
                    //     message: `Password Pattern`
                    //  }
                ]}
            > */}
                <input ref={refPrefix} name="prefix" disabled={true} style={{ color: "black" }} defaultValue={props.prefix} onChange={onInputChange}/>
            {/* </Forma.Item> */}

            {/* <Forma.Item
                label="Opis"
                name="desc"
                rules={[

                    {
						validator(rule, value, cb) {
						  if (!value) {
							cb("Pole Opis jest wymagane!");
							setErrorDesc(true);
						   }
						   else
						   setErrorDesc(false);
						  //callback();
						}
					  }
                    // {
                    //     required: true,
                    //     message: 'Pole Opis jest wymagane!',
                    // }
                    // { min: 5, message: 'Username must be minimum 5 characters.' },
                    // {
                    //     pattern:/^[0-9]*$/,
                    //     message: `Password Pattern`
                    //  }
                ]}
            > */}
                {/* <Input ref={refDesc} name="desc" defaultValue={props.desc} onChange={onInputChange}/>
            </Forma.Item> */}

            {/* <Forma.Item
                label="Tag"
                name="tag"
                rules={[

                    {
						validator(rule, value, cb) {
						  if (!value) {
							cb("Pole Tag jest wymagane!");
							setErrorTag(true);
						   }
						   else
						   setErrorTag(false);
						  //callback();
						}
					  }
                    // {
                    //     required: true,
                    //     message: 'Pole Tag jest wymagane!',
                    // }
                    // { min: 5, message: 'Username must be minimum 5 characters.' },
                    // {
                    //     pattern:/^[0-9]*$/,
                    //     message: `Password Pattern`
                    //  }
                ]}
            > */}
                <input ref={refTag} name="tag" defaultValue={props.tag} onChange={onInputChange}/>
            {/* </Forma.Item> */}
            {/* <Forma.Item label="Edytuj"> */}
            <checkbox name="checkbox" checked={checkVal&&!(errorDesc||errorTag)}  onClick={onCheckClick}/>
            {/* <Checkbox checked={checkVal}  onClick={onCheckClick}/> */}
            {/* </Forma.Item> */}


        {/* </Forma> */}
        {/* </Card> */}
        </>
        // </BorderWrapper>

        // </div>
        // </div>


    )
}

export default MultiItem;